'use client'


export default function PromptBox() {
    return <form className={`w-full ${false ? "max-w-3xl":" max-w-2xl"} bg-white/30 backdrop-blur-md drop-shadow-lg border-white border-2  rounded-3xl mt-4 transition-all shadow-2xs p-4 `}> 
        <textarea
        className="outline-none overflow-hidden resize-none bg-transparent break-words w-full"
        rows={2}
        placeholder="Type here your fav lyrics"
        
        />  
    </form>
}